body {
  margin: 0;
  padding: 0;
  background: black;
  font-family: 'Aladin', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 72px;
  color: gold;
  text-shadow:
    0 0 5px #fff,
    0 0 10px #fff,
    0 0 20px #fff,
    0 0 40px gold,
    0 0 80px gold,
    0 0 90px gold,
    0 0 100px gold,
    0 0 150px gold;
}
h2 {
  font-size: 72px;
}
.App {
  text-align: center;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  vertical-align: middle;
  -webkit-justify-content: center;
          justify-content: center;
}

.App-fadeIn {
  background: url(/iry/static/media/bg.4f917206.jpg) no-repeat 50% fixed;
  background-size: cover;
  -webkit-animation: Background-fade 3s ease-in;
          animation: Background-fade 3s ease-in;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 120px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: white;
}
.App-link-hidden {
  display: none;
}

.App-noselect {
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes Background-fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes Background-fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
